<style scoped>
  #introduce{
          position: relative;
          width: 100%;
      }

      #introduce p{
          position: relative;
          width: 100%;
          text-align: center;
          opacity: 0;
      }



      #title{
          font-size: 50px;/*px*/
          font-family: Comic Sans MS;
          margin-top: 10%;
          top: 30px;
          -webkit-transition: opacity 2.4s, top 2.4s;
      }


      #word{

          font-size: 40px;/*px*/
          margin-top: 60px;
          top: 30px;
          -webkit-transition: opacity 2s, top 2s;
   }

</style>

<template>
    <div id="introduce">
        <p id="title" :style="classA">ali-village</p>
        <p id="word" :style="classB">欢迎来到阿里乡</p>
    </div>
</template>

<script>
  module.exports = {
    props: [''],
    components: {

    },
    data: function () {
      return {
          classA: {},
          classB: {}
      }
    },
    methods: {
            onload: function(){
                var that = this;
                setTimeout(function(){
                    that.classA = {
                        opacity: "1",
                        top: "0px",
                    };
                }, 10);
                setTimeout(function(){
                    that.classB=that.classA;
                },2000);
            }
    },
    ready: function () {
    }
  }
</script>



