<style scoped>
    #content{
        position: relative;
    }

    .tip{
        width: 50%;
        position: relative;
        display: inline-block;
        margin-top: 100px;
    }

    .choose{
        position: absolute;
        width: 138px;
        height: 138px;
        border:1px solid #000;
        margin-left: 50%;
        left: -70px;
        background-image: url(../images/choose.png);
        background-size: 100%;
        z-index: 1;
        opacity: 0;
    }

    .tipImg{
        position: relative;
        width: 100px;
        height: 100px;
        background-size: 100%;
        border-width: 20px;
        border-style: solid;
        border-radius: 70px;
        margin: auto;
        top: 0px;
        transform: rotate(0deg);
        -webkit-animation: ani1 10s;
    }

    .tipTitle{
        position: relative;
        width: 100%;
        text-align: center;
        margin-top: 20px;
        font-size: 36px;/*px*/
    }

    @-webkit-keyframes ani1{
        0%{
            transform: rotate(0deg);
        }
        15%{
            transform: rotate(105deg);
        }
        45%{
            transform: rotate(-90deg);
        }
        60%{
            transform: rotate(180deg);
        }

    }
</style>

<template>
    <div id="content">
            <div class="tip" v-for="item in items">
                <div class="choose" @click="chooseOk"></div>
                <div class="tipImg" :style="{backgroundImage:item.imgUrl, borderColor: item.borderColor}" ></div>
                <div class="tipTitle">{{item.title}}</div>
            </div>
    </div>
</template>

<script>
  module.exports = {
    props: [''],
    components: {
    },
    data: function () {
      return {
            items: [
                {
                    imgUrl: "url(../images/literature.png)",
                    borderColor: "#feaa90",
                    title: "文艺"
                },{
                    imgUrl: "url(../images/sport.png)",
                    borderColor: "#f08d70",
                    title: "运动"
                },{
                    imgUrl: "url(../images/interest.png)",
                    borderColor: "#84d3b6",
                    title: "趣味"
                },{
                    imgUrl: "url(../images/outdoors.png)",
                    borderColor: "#73ccac",
                    title: "户外"
                },{
                    imgUrl: "url(../images/life.png)",
                    borderColor: "#00afa8",
                    title: "生活"
                },{
                    imgUrl: "url(../images/science.png)",
                    borderColor: "#019e95",
                    title: "科普"
                }
            ],

            chooseFlag: 1,
      }
    },
    methods: {
        chooseOk: function(e){
            var that = this;
            var target = e.target;
            if(that.chooseFlag%2!=0){
                target.style.opacity = "1";
            }else{
                target.style.opacity = "0";
            }
            that.chooseFlag++;
        }
    },
    ready: function () {
    }
  }
</script>



